import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import axios from "axios";

const Home = () => {
  const [posts, setPosts] = useState([]);

  const cat = useLocation().search

  // Version locale 

  // useEffect(() => {
  //   const fetchData = async () => {
  //     try {
  //       const res = await axios.get(`/posts/${cat}`);
  //       setPosts(res.data);

  //      // console.log(res.data);
  //     } catch (err) {
  //       console.log(err);
  //     }
  //   };
  //   fetchData();
  // }, [cat]);

  // Version production

  useEffect(() => {
    const fetchData = async () => {
      try {
        let url = 'api/posts'; // URL par défaut pour récupérer tous les posts
        
        // Vérifier si cat est défini, sinon récupérer tous les posts
        if (cat) {
          url = `api/posts/${cat}`;
        }
        
        const res = await axios.get(url);
  
        if (!res.headers['content-type'] || !res.headers['content-type'].includes('application/json')) {
          throw new Error('Response is not in JSON format');
        }
  
        setPosts(res.data);
      } catch (err) {
        console.log(err);
        console.error('Erreur Axios:', err.message);
      }
    };
    fetchData();
  }, [cat]);  
 
  const getText = (html) =>{
    const doc = new DOMParser().parseFromString(html, "text/html")
    return doc.body.textContent
  }

 {/*
    Conteneur de messages 
    *********************

    Pour chaque message, on va retourner les détails 
    ************************************************
    On utilise ".map()", on donne une clé unique, qui est l'identifiant de post, tous les identifiants sont différents, on peut les utiliser et à l'intérieur de la div, on aura un conteneur d'image qui va inclure l'image, et on aura un contenu. Cela va inclure le titre et la description.

  */}
  return (   
    <div className="home">
      <div class="alert-danger">⚠ Ce site est un site de démonstration, contenant des données fictives, et ayant pour unique objectif, de présenter mes compétences en React JS et Node JS</div>
      <div className="posts">
      {Array.isArray(posts) && 
          posts.map((post) => (
            <div className="post" key={post.id}>
              <div className="img">
                <img src={`../upload/${post.img}`} alt="" />
              </div>
              <div className="content">
                {/* Récupération de la publication par l'id  */}
                <Link className="link" to={`/post/${post.id}`}>
                   {/* Récupération du titre de la publication */}
                  <h1>{post.title}</h1>
                </Link>
                {/* Récupération de la description de la publication */}
                <p>{getText(post.desc)}</p>
                 {/* Bouton qui permettra de lire toute la description */}
               {/* <button>Lire plus</button> */}
              </div>
            </div>
          ))
        }
      </div>
    </div>
  );
};

export default Home;